___CSS_LOADER_EXPORT___.push([module.id, `/* Header Section */

.header_section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.75rem;
}

.header_main {
    display: flex;
    align-items: center;
    gap: .5rem;
}

.header_main i {
    font-size: 1.25rem;
}

.main_title {
    font-size: 1.5rem;
    font-weight: 500;
}

.create_task_button {
    font-size: 1rem;
    font-family: 'Karla';
    padding: .1rem;
}

/* SubHeader Section */ 

.subheader_section {
    display: flex;
    justify-content: space-between;
    padding: .5rem 2rem;
}

.subheader_list {
    display: flex;
    gap: .5rem;
    align-items: center;
}

.todo_control_button {
    font-size: 1rem;
    background: none;
    border: none;
}

.subheader_list {
    display: flex;
    justify-content: space-between;
}

.todo_header_section {
    display: flex;
    align-items: center;
}

.search_input_item {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: .25rem;
    border: 1.5px solid #000;
    border-radius: .25rem;
}

.search_image {
    font-size: 1.35rem;
}

.search_input {
    height: 1.5rem;
    width: 14rem;
    border: none;
}

.search_input::placeholder {
    font-family: Karla;
    font-size: 1.1rem;
    color: #000;
}

/* End of SubHeader Section */ 

/* Todo Card Section */

.todo_main {
    padding: 1rem 2rem;
}`, "",{"version":3,"sources":["webpack://./src/assets/styles/main.css"],"names":[],"mappings":"AAAA,mBAAmB;;AAEnB;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,UAAU;AACd;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;AACpB;;AAEA;IACI,eAAe;IACf,oBAAoB;IACpB,cAAc;AAClB;;AAEA,sBAAsB;;AAEtB;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,UAAU;IACV,mBAAmB;AACvB;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,8BAA8B;AAClC;;AAEA;IACI,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,WAAW;IACX,wBAAwB;IACxB,qBAAqB;AACzB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,cAAc;IACd,YAAY;IACZ,YAAY;AAChB;;AAEA;IACI,kBAAkB;IAClB,iBAAiB;IACjB,WAAW;AACf;;AAEA,6BAA6B;;AAE7B,sBAAsB;;AAEtB;IACI,kBAAkB;AACtB","sourcesContent":["/* Header Section */\n\n.header_section {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    padding: 1.75rem;\n}\n\n.header_main {\n    display: flex;\n    align-items: center;\n    gap: .5rem;\n}\n\n.header_main i {\n    font-size: 1.25rem;\n}\n\n.main_title {\n    font-size: 1.5rem;\n    font-weight: 500;\n}\n\n.create_task_button {\n    font-size: 1rem;\n    font-family: 'Karla';\n    padding: .1rem;\n}\n\n/* SubHeader Section */ \n\n.subheader_section {\n    display: flex;\n    justify-content: space-between;\n    padding: .5rem 2rem;\n}\n\n.subheader_list {\n    display: flex;\n    gap: .5rem;\n    align-items: center;\n}\n\n.todo_control_button {\n    font-size: 1rem;\n    background: none;\n    border: none;\n}\n\n.subheader_list {\n    display: flex;\n    justify-content: space-between;\n}\n\n.todo_header_section {\n    display: flex;\n    align-items: center;\n}\n\n.search_input_item {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    gap: .25rem;\n    border: 1.5px solid #000;\n    border-radius: .25rem;\n}\n\n.search_image {\n    font-size: 1.35rem;\n}\n\n.search_input {\n    height: 1.5rem;\n    w