    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style-type: none;
}

:root {
    --main_menu_title: 1.1rem;
}

/* Body Section */

body {
    border: 1px solid #000;
    font-family: 'Karla';
    font-size: 62.5%;
}

/* Container Section */

.container {
    border: 1px solid #000;
    display: grid;
    grid-template-areas: "aside main main main main main";
    grid-template-columns: 14rem repeat(5, 1fr);
    height: 100vh;
}

/* Aside Section */

aside {
    border: 1px solid #000;
    grid-area: aside;
    display: flex;
    margin: 0 auto;
}

/* Nav Section */

.nav_menu {
    height: 100%;
    display: flex;
    flex-direction: column;
}

.logo_container {
    padding: 1.5rem 0;
}

.nav_logo i {
    font-size: 2rem;
    font-weight: 500;
}

.nav_logo {
    font-size: 1.5rem;
    font-weight: 500;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: .25rem;
}

.nav_menu button {
    background-color: transparent;
    border: none;
}

.nav_list {
    display: none;
}

.menu_title {
    font-size: 1.2rem;
    font-weight: 500;
    padding: .75rem 0;
}

.pinned_projects_menu {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.main_menu_button {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: var(--main_menu_title);
    gap: .35rem;
}

.dashboard_menu {
    padding: 1.25rem 0;
}

.dashboard_button_item {
    display: flex;
    align-items: center;
    gap: .35rem;
    font-size: var(--main_menu_title);
}

.first_icon {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 25px;
    width: 25px;
    font-size: 1.25rem;
}

.second_icon {
    height: 25px;
    width: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
}

.todo_main_menu {
    display: flex;
    flex-direction: column;
    gap: .75rem;
}

.setting_menu_button {
    display: flex;
    align-items: center;
}

/* Main Section */

main {
    border: 1px solid #000;
    grid-area: main;
    height: 100%;
}

